
.modal-enter {
  opacity: 0;
}
.modal-leave-active {
  opacity: 0;
}
.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  display: table;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}
.modal-mask .modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}
.modal-mask .modal-wrapper .modal-container {
  width: 26%;
  min-width: 260px;
  margin: 0px auto;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.33);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
}
.modal-mask .modal-wrapper .modal-container .modal-header,
.modal-mask .modal-wrapper .modal-container .modal-body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.modal-mask .modal-wrapper .modal-container .modal-header {
  font-size: 16px;
  min-height: 40px;
  padding: 10px 20px;
  background: #298ce5;
  color: #fff;
}
.modal-mask .modal-wrapper .modal-container .modal-body {
  font-size: 12px;
  min-height: 80px;
}
.modal-mask .modal-wrapper .modal-container .modal-footer {
  padding-bottom: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
  font-size: 12px;
}
.modal-mask .modal-wrapper .modal-container .modal-footer .modal-default-button {
  background: #298ce5;
  color: #fff;
  padding: 2px 10px;
  margin-right: 10px;
  font-size: 12px;
  -webkit-appearance: none;
  border-radius: 1px;
  border: none;
  outline: none;
}


