
.app-vue {
  font-size: 0px;
}
.app-vue .app-tool {
  display: none;
}
@media only screen and (max-width: 640px) {
.app-vue .app-tool {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
}
.app-vue .app-slide {
  position: fixed;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 250px;
}
@media only screen and (max-width: 640px) {
.app-vue .app-slide {
    display: none;
}
}
.app-vue .app-content-wrapper {
  position: fixed;
  left: 250px;
  top: 0px;
  bottom: 0px;
  right: 0px;
  z-index: 0;
}
@media only screen and (max-width: 640px) {
.app-vue .app-content-wrapper {
    left: 0px;
    top: 50px;
}
}


