
.list-vue {
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  overflow: auto;
}
.list-vue .item-wrapper {
  margin: 0px 20px;
  padding-top: 20px;
  padding-bottom: 5px;
  position: relative;
}
.list-vue .item-wrapper:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid #ddd;
  content: ' ';
}
.list-vue .item-wrapper:last-child:after {
  display: none;
}
.list-vue .item-wrapper .title-wrapper,
.list-vue .item-wrapper .content-wrapper {
  padding: 5px 0px;
}
.list-vue .item-wrapper .time-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
}
.list-vue .page-wrapper {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 55px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding-left: 20px;
  padding-right: 20px;
}
.list-vue .page-wrapper:before {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  border-top: 1px solid #ddd;
  content: ' ';
}
.list-vue .page-wrapper .page-control {
  font-size: 16px;
  color: #298ce5;
}


