
.editor-vue {
  background: #f4f5f5;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 12px;
  padding: 0px 5px;
}
.editor-vue .item {
  padding: 10px;
  display: block;
  width: 100%;
  margin-top: 10px;
  font-size: 14px;
  line-height: 18px;
  box-shadow: none;
  border: 1px solid #ddd;
  border-radius: 2px;
  -webkit-transition: border 0.3s;
  transition: border 0.3s;
  background-color: #fff;
  color: #222;
  resize: none;
  outline: none;
  vertical-align: top;
}
.editor-vue .title {
  height: 40px;
}
.editor-vue .des {
  height: 70px;
}
.editor-vue .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.editor-vue .sort-wrapper {
  margin-top: 10px;
}
.editor-vue .sort-wrapper .sort-title {
  color: #222;
  font-size: 12px;
}
.editor-vue .sort-wrapper .sort-content {
  font-size: 12px;
  margin-left: 10px;
  padding: 1px 5px;
  color: #909090;
  background: #fff;
  border: 1px solid #f1f1f1;
}
.editor-vue .sort-wrapper .checked-sort {
  background: #298ce5;
  border: none;
  color: #fff;
}
.editor-vue .button {
  width: 100%;
  max-width: 300px;
  padding: 5px;
  background: #298ce5;
  margin: 10px 0px;
  font-size: 12px;
  color: #fff;
  -webkit-appearance: none;
  border-radius: 2px;
  border: none;
  outline: none;
}
.pic-pos {
  width: 100%;
  margin-top: 10px;
}
.pic-pos .input-none {
  display: none;
}


