
.body-content {
  width: 100%;
  padding: 10px;
}
.body-content .app-input {
  padding: 3px 10px;
  width: 100%;
  margin-top: 10px;
  font-size: 14px;
  line-height: 18px;
  box-shadow: none;
  border: 1px solid #ddd;
  border-radius: 2px;
  -webkit-transition: border 0.3s;
  transition: border 0.3s;
  background-color: #fff;
  color: #222;
  resize: none;
  outline: none;
  vertical-align: top;
}
.body-content .app-input:first-child {
  margin-top: 0px;
}


