
.about-vue {
  font-size: 16px;
  color: #6495ed;
}
.about-vue .bot {
  position: fixed;
  left: 250px;
  z-index: 1px;
  background: #fff;
  bottom: 0px;
  right: 0px;
  height: 60px;
  z-index: 0;
}
@media only screen and (max-width: 640px) {
.about-vue .bot {
    left: 0px;
}
}


