
.tag-vue {
  padding-left: 20px;
  padding-top: 20px;
}
.tag-vue .tag-wrapper {
  margin-top: 20px;
}
.tag-vue .tag-wrapper .tag {
  font-size: 20px;
  color: #298ce5;
  border: 1px solid #298ce5;
  border-radius: 4px;
  display: inline-block;
  font-size: 14px;
  height: 40px;
  line-height: 40px;
  margin: 0 15px 10px 0;
  padding: 0 15px;
  text-decoration: none;
}


